$hamburgerColor: #e9eae2;
$hamburgerColorActive: #f8c255;

.hamburger {
  display: block;
  background: none;
  cursor: pointer;
  border: 3px solid $hamburgerColor;
  transition: all .8s;

  &:hover, &:active {
    border-color: $hamburgerColorActive;

    .hamburger__element {
      background-color: $hamburgerColorActive;

      &:before,
      &:after {
        background-color: $hamburgerColorActive;
      }
    }
  }

  &:focus {
    border-color: $hamburgerColorActive;
  }
}

.hamburger__element {
  width: 40px;
  height: 3px;
  display: block;
  background-color: $hamburgerColor;
  margin: 0 auto;
  transition: all .8s;

  &:before,
  &:after {
    content: '';
    width: 40px;
    height: 3px;
    display: block;
    background-color: $hamburgerColor;
    margin: 0 auto;
    transition: all .8s;
  }

  &:before {
    transform: translateY(-7px);
  }

  &:after {
    transform: translateY(4px);
  }
}

.hamburger--active {
  position: relative;
  z-index: 1;
  border-color: $hamburgerColorActive;

  &:hover, &:active {
    border-color: $hamburgerColorActive;

    .hamburger__element {
      background-color: $hamburgerColor;

      &:before,
      &:after {
        background-color: $hamburgerColor;
      }
    }
  }

  .hamburger__element {
    transform: rotate(45deg);
    transition: all .8s;

    &:before {
      transform: rotate(-90deg);
      position: relative;
      z-index: 1;
      transition: all .8s;
    }

    &:after {
      opacity: 0;
      transform: rotate(-90deg);
      transition: all .8s;
    }
  }
}

@media (max-width: 480px) {
  .hamburger__element {
    width: 30px;

    &:before,
    &:after  {
      width: 30px;
    }
    
  }
}
