$bgStudio: #ff9b6d;

.studio {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
}

.studio__name, .studio__staff {
  grid-column: span 5;
}

.studio__info, .studio__persons {
  grid-column: span 11;
}

.studio__name {
  background-color: $bgStudio;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
  }
}

.studio__title {
  position: relative;
  z-index: 1;
  font-size: 48px;
  color: $white;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1;
  margin: 63.2% 0 35.4% 46.6%;

  @media (min-width: 1500px) {
    @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
  }
}

.studio__info {
  display: flex;
  padding: 9.1% 15.9% 4.3% 9.6%;
}

.studio__information {
  flex-basis: 48%;
  margin-right: 4%;
}

.studio__description {
  flex-basis: 48%;
}

.studio__text {
  font-size: 18px;
  font-family: "roboto_condensedlight";
  color: $colorText;
  line-height: 1;
  margin: 5.4% 0;

  &:first-child {
    margin-top: 0;
  }

  @media (min-width: 1500px) {
    @include dynamicText(112.5%, 36, 18, 1500px, 3000, 1500);
  }
}

.studio__text--bold-italic {
  font-family: 'roboto_condenseditalic';
}

.studio__img {
  display: block;
  width: 100%;
}

.studio__subtitle {
  font-size: 14px;
  font-family: "roboto-condensedbold";
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.286;
  margin: 9.4% 0 5%;
  border-bottom: 1px solid rgba(0, 0, 0, .18);

  @media (min-width: 1500px) {
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }
}

.studio__list {
  list-style: none;
  padding: 0;
  display: flex;
  margin: 2.5% 0;
}

.studio__list--item {
  font-size: 12px;
  color: $colorText;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1.167;

  &:first-child {
    margin-right: 9.4%;
  }

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

.studio__staff {
  padding: 12.9% 6.4% 19.3% 35.4%;
}

.studio__staff--title {
  font-size: 14px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1;
  margin: 0 0 7.4% 0;

  @media (min-width: 1500px) {
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }
}

.studio__staff--text {
  font-size: 14px;
  color: $colorText;
  font-family: 'roboto_condensedlight';
  line-height: 1;
  margin: 7.4% 0;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 1500px) {
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }
}

.studio__persons {
  padding-top: 5.7%;
  padding-right: 15.6%;
}

@media (max-width: 1500px) {

  .studio__info, .studio__persons {
    padding-right: 2%;
  }
}

@media (max-width: 1300px) {

  .studio__title {
    margin-top: 73.2%;
    margin-bottom: 25.4%;
    margin-left: 50%;
  }
}

@media (max-width: 980px) {

  .studio__name {
    grid-column: span 16;
  }

  .studio__info {
    grid-column: span 16;
    padding-left: 15px;
    padding-right: 15px;
  }

  .studio__title {
    margin: 10% 0 30% 10%;
  }

  .studio__staff {
    padding-left: 15px;
    padding-right: 4%;
  }

  .studio__persons {
    padding-right: 15px;
    padding-bottom: 10%;
  }
}

@media (max-width: 768px) {

  .studio__title {
    margin-bottom: 40%;
    font-size: 38px;
  }

  .studio__staff {
    padding-top: 2%;
    padding-bottom: 2%;
  }

  .studio__persons {
    padding-top: 2%;
  }

  .studio__staff--title, .studio__staff--text {
    margin: 3% 0;
  }
}

@media (max-width: 650px) {

  .studio {
    grid-template-columns: 1fr;
  }

  .studio__name, 
  .studio__info,
  .studio__staff,
  .studio__persons {
    grid-column: span 1;
  }

  .studio__info {
    flex-direction: column;
  }

  .studio__staff {
    padding-right: 15px;
  }

  .studio__persons {
    padding-left: 15px;
  }
}






