$colorCopy: #191919;

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.footer__map {
  grid-column: span 1;
}

.footer__info {
  grid-column: span 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__left {
  margin-left: 15.3%;
  margin-top: 20%;
}

.footer__right {
  margin-top: 20%;
  margin-right: 22%;
}

.footer__logo {
  display: block;
  max-width: 100%;
}

.footer__slogan {
  font-size: 18px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1; 
  margin: 0;
  margin-top: 5px;

  @media (min-width: 1500px) {
    @include dynamicText(112.5%, 36, 18, 1500px, 3000, 1500);
  }
}

.footer__socials {
  margin-top: 60px;
  margin-left: 15px;
  margin-bottom: 15px;
}

.footer__address {
  font-size: 13px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.077;
  margin: 0 0 10% 0;

  @media (min-width: 1500px) {
    @include dynamicText(81.25%, 26, 13, 1500px, 3000, 1500);
  }
}

.footer__contacts {
  font-size: 13px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.077;
  margin: 0;

  @media (min-width: 1500px) {
    @include dynamicText(81.25%, 26, 13, 1500px, 3000, 1500);
  }
}

.footer__tel, 
.footer__mail {
  color: $colorText;
  text-decoration: none;
  display: inline-block;
  transition: all .8s;

  &:hover {
    color: $activeTextColor;
  }
}

.footer__copy {
  font-size: 12px;
  color: $colorCopy;
  line-height: 1.8;
  text-align: center;
  flex-basis: 100%;
  margin: 9% 0 7% 0;

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

@media (max-width: 1500px) {

  .footer__right {
    margin-right: 2%;
  }
}

@media (max-width: 980px) {

  .footer__info {
    margin-left: 15px;
    margin-right: 15px;
  }

  .footer__left {
    margin-left: 0;
  }

  .footer__right {
    margin-right: 0;
  }
}

@media (max-width: 768px) {

  .footer {
    grid-template-columns: 1fr;
  }

  .footer__map {
    padding-bottom: 80%;
  }

  .footer__left {
    margin-top: 12%;
    margin-left: 7%;
  }
  
  .footer__right {
    margin-top: 12%;
    margin-right: 7%;
  }
}

@media (max-width: 480px) {

  .footer__info {
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
  }

  .footer__left {
    text-align: center;
    margin-left: 0;
  }

  .footer__right {
    text-align: center; 
    margin-right: 0; 
  }

  .footer__socials {
    margin-left: 0;
    justify-content: center;
  }
}