$bgnews: #d4edbf;
$colorNews: #6a6969;

.news {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
}

.news__name {
  grid-column: span 5;
  background-color: $bgnews;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
  }
}

.news__title {
  position: relative;
  z-index: 1;
  font-size: 48px;
  color: $white;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1;
  margin: 63.2% 0 35.4% 46.6%;

  @media (min-width: 1500px) {
    @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
  }
}

.news__info {
  grid-column: span 11;
  display: flex;
  align-items: center;
  padding-left: 9.6%;
  padding-right: 15.9%;
}

.news__list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.news__element {
  display: flex;
  flex-direction: column;
  flex-basis: 48%;

  &:first-child {
    margin-right: 4%;
  }
}

.news__subtitle {
  font-size: 24px;
  font-family: "roboto_condensedbold";
  color: $colorText;
  text-transform: uppercase;
  line-height: 1;
  min-height: 50px;
  margin: 0;

  @media (min-width: 1500px) {
    @include dynamicText(150%, 48, 24, 1500px, 3000, 1500);
  }
}

.news__date {
  font-size: 12px;
  color: $colorNews;
  text-transform: uppercase;
  line-height: 1.2;
  margin: 0 0 4.5% 0;

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

.news__img {
  display: block;
  width: 100%;
}

.news__description {
  font-size: 12px;
  color: $colorNews;
  line-height: 1.167;
  text-align: justify;
  margin: 5% 0 2% 0;
  
  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

.news__href {
  text-align: right;
  margin: auto 0 0 0;
}

.news__link {
  font-size: 12px;
  color: $colorPersonIcon;
  text-transform: uppercase;
  line-height: 1.167;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color .8s;

  &:hover {
    border-bottom: 1px solid $colorPersonIcon;
  }

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

@media (max-width: 1500px) {

  .news__info {
    padding-right: 2%;
  }
}

@media (max-width: 1300px) {

  .news__title {
    margin-top: 73.2%;
    margin-bottom: 25.4%;
    margin-left: 50%;
  }
}

@media (max-width: 980px) {

  .news__name {
    grid-column: span 16;
  }

  .news__title {
    margin: 10% 0 30% 10%;
  }

  .news__info {
    grid-column: span 16;
    padding: 8% 15px 10%;
  }
}

@media (max-width: 768px) {

  .news__title {
    margin-bottom: 40%;
    font-size: 38px;
  }
}

@media (max-width: 580px) {
  .news__list {
    flex-direction: column;
  }

  .news__element {
    flex-basis: auto;

    &:first-child {
      margin-right: 0;
      margin-bottom: 5%;
    }
  }

  .news__subtitle {
    min-height: auto;
    margin-bottom: 5px;
  }
}




