$menuBg: #f8c255;

.menu {
  position: absolute;
  top: 50px;
  right: 11%;
}

.menu__hamburger {
  width: 119px;
  height: 114px;
}

.menu__list {
  display: none;
  align-items: center;
  background-color: $menuBg;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  min-width: 725px;
  padding-right: 119px;
  padding-left: 25px;
  box-sizing: border-box;
  z-index: -9999;
}

.menu__list--active {
  display: flex;
  z-index: 1;
  animation-duration: .8s;
  animation-name: menu;
}

.menu__link {
  font-size: 14px;
  color: $white;
  text-transform: uppercase;
  line-height: 1.786;
  text-decoration: none;
  padding: 10px 14px;
  margin: 0 14px;
  transition: all .8s;
  position: relative;

  @media (min-width: 1500px) {
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }

  &:after {
    content: '';
    position: absolute;
    opacity: 0;
    left: 0;
    bottom: 17px;
    height: 2px;
    width: 0;
    background-color: $black;
    transition: all .8s;
  }

  &:hover {

    &:after {
      opacity: 1;
      width: 100%;
    }
    
  }

  &:hover, &:active {
    color: $black;
  }
}

@keyframes menu {
  0% {
    opacity: 0;
    transform-origin: right;
    transform: scale(0) translateX(20%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateX(0%);
  } 
}

@media (min-width: 1500px) {
  .menu__list {
    min-width: 1100px;
  }
}

@media (max-width: 980px) {
  .menu {
    right: 30px;
    top: 30px;
  }
}

@media (max-width: 768px) {
  .menu {
    height: 100%;
  }

  .menu__hamburger {
    width: 70px;
    height: 70px;
  }

  .menu__list--active {
    flex-direction: column;
    justify-content: center;
    padding-right: 0;
    padding-left: 0;
    min-width: 320px;
    overflow-y: auto;
  }

  .menu__link {
    display: inline-block;
    padding: 6px 10px;
    margin: 2px 0;
  }
}

@media (max-width: 480px) {
  .menu__hamburger {
    display: block;
    margin-left: auto;
    width: 50px;
    height: 50px;
  }

  .menu {
    left: 30px;
  }

  .menu__list {
    width: 100%;
    min-width: auto;
  }
}