$bgThree: #dfe0d9;
$bgFour: #f7f7f3;
$bgSix: #eaeaea;
$bgEight: #fcfcfc;
$bgNine: #cecece;
$colorLikeFive: #ce7c7f;

.projects {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
}

.projects__element--one {
  grid-column: span 5;
  grid-row: span 2;
  background-color: $thirty;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
  }

  .projects__title {
    font-size: 48px;
    color: $white;
    font-weight: normal;
    text-transform: uppercase;
    line-height: 1;
    margin: 12.9% 0 34.4% 46.2%;

    @media (min-width: 1500px) {
      @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
    }
  }

  .projects__links {
    margin-top: 36.5%;
    margin-left: calc(27.9% - 25px);
    margin-bottom: 12.9%;
    position: relative;
    z-index: 1;
  }
}

.projects__element--two {
  grid-column: span 6;
  grid-row: span 2;
  background: url('../img/image2.png') right top no-repeat $white;
  background-size: cover;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image2-2x.png');
  }

  .projects__info {
    margin-left: 17.5%;
    margin-top: 15.7%;
  }

  .projects__counter {
    margin-left: 19.2%;
    margin-top: 11.4%;
    margin-bottom: 12.2%;
  }
}

.projects__element--three {
  grid-column: span 5;
  grid-row: span 1;
  background: url('../img/image3.png') left center no-repeat $bgThree;
  background-size: cover;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image3-2x.png');
  }
  
  .projects__like {
    margin-top: 5.8%;
  }

  .projects__description {
    max-width: 38%;
  }

  .projects__info {
    margin-left: 8.6%;
    margin-top: 7.5%;
  }

  .projects__counter {
    margin-top: 4%;
    margin-left: 8.6%;
    margin-bottom: 4.5%;
  }
}

.projects__element--four {
  grid-column: span 5;
  grid-row: span 2;
  background: url('../img/image4.png') center top no-repeat $bgFour;
  background-size: 100%;
  padding-top: 73%;
  display: flex;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image4-2x.png');
  }

  .projects__info {
    margin-left: 12.9%;
    margin-right: 4.3%;
    margin-bottom: 7.5%;
  }

  .projects__counter {
    display: flex;
    align-items: flex-start;
    flex-grow: 1;
    flex-shrink: 0;
    margin-top: 31px;
    margin-bottom: 7.5%;
    margin-right: 3%;
  }

  .projects__like {
    margin-top: 0;
    margin-left: 10%;
  }
}

.projects__element--five {
  grid-column: span 3;
  grid-row: span 2;
  background: url('../img/image5.png') center top no-repeat;
  background-size: cover;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image5-2x.png');
  }

  .projects__info {
    margin-left: 10%;
    margin-top: 13%;
    margin-bottom: 13%;
  }

  .projects__view {
    color: $white;

    &:hover {

      span {
        color: $white;
      }

    }
  }

  .projects__description,
  .projects__name {
    color: $white;
  }

  .like__btn {

    svg {
      fill: $white;
    }
    
  }

  .projects__counter {
    margin-bottom: 73.3%;
    margin-left: 10%;
  }

  .like__counts {
    color: $colorLikeFive;
  }

  .projects__like {
    margin-top: 9.2%;
  }
}

.projects__element--six {
  grid-column: span 8;
  grid-row: span 2;
  background: url('../img/image6.png') center top no-repeat $bgSix;
  background-size: cover;
  display: flex;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image6-2x.png');
  }

  .projects__info {
    margin-left: 48.5%;
    margin-top: 12.1%;
    margin-bottom: 4%;
  }

  .projects__counter {
    margin-top: 12.3%;
    margin-left: 5%;
    margin-bottom: 4%;
  }

  .projects__like {
    margin-top: 12.5%;
  }
}

.projects__element--seven {
  grid-column: span 5;
  grid-row: span 1;
  background: url('../img/image7.png') left top no-repeat $white;
  background-size: 100%;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image7-2x.png');
  }

  .projects__info {
    margin-left: 52.6%;
    margin-top: 10.3%;
    margin-bottom: 5.3%;
  }

  .projects__counter {
    margin-left: 52.6%;
    margin-bottom: 5.3%;
  }

  .projects__like {
    margin-top: 11.5%;
  }
}

.projects__element--eight {
  grid-column: span 8;
  grid-row: span 1;
  background: url('../img/image8.png') top center no-repeat $bgEight;
  background-size: 100%;
  display: flex;
  padding-top: 27.2%;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image8-2x.png');
  }

  .projects__info {
    margin-left: 20.4%;
    margin-bottom: 2.7%;
  }

  .projects__counter {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-basis: 210px;
    margin-top: 31px;
    margin-left: 6.1%;
    margin-bottom: 2.7%;
  }

  .projects__like {
    margin-top: 0;
  }
}

.projects__element--nine {
  grid-column: span 3;
  grid-row: span 1;
  background: url('../img/image9.png') left top no-repeat $bgNine;
  background-size: 100%;

  @media (min-resolition: 2dppx), (min-width: 1500px) {
    background-image: url('../img/image9-2x.png');
  }

  .projects__info {
    margin-left: 50%;
    margin-top: 25%;
    margin-bottom: 8.3%; 
  }

  .projects__counter {
    margin-left: 40%;
    margin-bottom: 11.6%;
  }

  .projects__like {
    margin-top: 14.7%;
  }
}

.projects__element--ten {
  grid-column: span 5;
  grid-row: span 1;
  background-color: $thirty;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="65%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
  }

  .projects__subtitle {
    font-size: 14px;
    color: $colorText;
    text-transform: uppercase;
    line-height: 1;
    margin: 11.8% 0 0 21.5%;

    @media (min-width: 1500px) {
      @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
    }

  }

  .projects__links {
    position: relative;
    z-index: 1;
    margin-left: calc(21.5% - 25px);
    margin-bottom: 32.2%;
  }
}

.projects__name { 
  font-size: 24px;
  font-family: "roboto_condensedlight";
  color: $colorText;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1; 
  margin: 0 0 10px 0;

  @media (min-width: 1500px) {
    @include dynamicText(150%, 48, 24, 1500px, 3000, 1500);
  }
}

.projects__description {
  font-size: 12px;
  font-family: "roboto_condensedlight";
  color: $colorText;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1;
  margin: 0;

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
  
  b {
    font-family: "roboto_condensedbold";
    font-weight: normal;
  }
}

.projects__description--lowercase {
  text-transform: initial;
}

.projects__view {
  font-size: 18px;
  font-family: "roboto_condensedlight";
  color: $colorText;
  text-transform: uppercase;
  line-height: 1;
  text-decoration: none;
  display: inline-block;
  background: url('../img/arrow-view.svg') left center no-repeat;
  padding-left: 25px;

  @media (min-width: 1500px) {
    @include dynamicText(112.5%, 36, 18, 1500px, 3000, 1500);
  }

  span {
    border-bottom: 1px solid transparent;
    transition: all .8s;
  }

  &:hover {

    span {
      border-color: $colorText;
    }

  }
}

.projects__like {
  max-width: 145px;
  text-align: center;
  margin: 22% 0 0 0;
}

@media (max-width: 1330px) {
  
  .projects__element--one {
    .projects__title {
      font-size: 36px;
    }
  }

  .projects__element--three {
    background-size: 100%;

    .projects__description {
      max-width: 44%;
    }

  }

  .projects__element--four {

    .projects__info {
      margin-left: 6%;
    }

    .projects__counter {
      flex-direction: column;
      align-items: center;
      margin-top: 0;
    }

    .projects__like {
      margin-top: 10%;
    }
  }

  .projects__element--six {

    .projects__info {
      margin-left: 36.5%;
    }
  }

  .projects__element--eight {

    .projects__counter {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      margin-top: 0;
      margin-right: 6%;
    }

    .projects__like {
      margin-top: 12%;
    }

  }

  .projects__element--nine {

    .projects__counter {
      margin-left: 28%;
    }

  }

  .projects__element--ten {

    .projects__subtitle {
      margin-left: 18.5%;
    }

    .projects__links {
      margin-left: calc(18.5% - 25px);
    }

  }

}

@media (max-width: 980px) {

  .projects__element--one {
    grid-column: span 7;

    .projects__title {
      font-size: 30px;
    }

    .projects__links {
      margin-left: 18%;
    }
  }

  .projects__element--two {
    grid-column: span 9;
  }

  .projects__element--three {
    grid-column: span 7;
    grid-row: span 2;

    .projects__description {
      max-width: 90%;
    }
  }

  .projects__element--four {
    grid-column: span 9;
  }

  .projects__element--five {
    grid-column: span 5;    
  }

  .projects__element--six {
    grid-column: span 11; 
    
    .projects__info {
      margin-left: 42.5%;
    }
  }

  .projects__element--seven {
    grid-column: span 7;   
  }

  .projects__element--eight {
    grid-column: span 9;   
  }

  .projects__element--nine {
    grid-column: span 9;
    
    .projects__counter {
      margin-left: 50%;
    }
  }

  .projects__element--ten {
    grid-column: span 7;   
  }

}

@media (max-width: 768px) {

  .projects__element--one {

    .projects__links {
      margin-left: 8%;
    }

    .projects__title {
      font-size: 26px;
    }
    
  }

  .projects__element--two {

    .projects__info {
      margin-left: 6%;
      margin-top: 8%;
    }

    .projects__counter {
      margin-left: 6%;
    }

  }

  .projects__element--three {
    background-position: 0 70%;

    .projects__info {
      margin-left: 6%;
      margin-top: 8%;
    }

    .projects__counter {
      margin-left: 6%;
    }

  }

  .projects__element--six {
    background-position: 50% 60px;

    .projects__info {
      margin-left: 30%;
      margin-top: 10%;
    }

    .projects__counter {
      margin-top: 30%;
      margin-left: 0;
    }
  }

  .projects__element--seven {

    .projects__counter {
      margin-left: 42%;
    }

  }

  .projects__element--eight {

    .projects__info {
      margin-left: 6%;
    }
  }

} 

@media (max-width: 580px) {

  .projects {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  .projects__element--one, .projects__element--two, .projects__element--three,
  .projects__element--four, .projects__element--five, .projects__element--six,
  .projects__element--seven, .projects__element--eight, .projects__element--nine,
  .projects__element--ten {
    grid-column: span 1;
    grid-row: span 1;
  }

  .projects__element--two {

    .projects__counter {
      margin-bottom: 8%;
    }
  }

  .projects__element--three {

    .projects__description {
      max-width: 45%;
    }

    .projects__counter {
      margin-bottom: 8%;
    }

  }

  .projects__element--four {

    .projects__info {
      margin-bottom: 8%;
    }

    .projects__counter {
      margin-bottom: 8%;
    }

    .projects__like {
      margin-top: auto;
    }
  }

  .projects__element--five {
    background-position: bottom center;

    .projects__info {
      margin-bottom: 5%;
    }
  }

  .projects__element--six {
    display: flex;
    flex-direction: column;
    background-position: -60px 0;

    .projects__info {
      margin-top: 8%;
      margin-left: 40%;
      margin-right: auto;
    }

    .projects__counter {
      margin: 4% auto 8% 62%;
    }

  }

  .projects__element--seven {
    .projects__counter {
      margin-left: 52.6%;
      margin-bottom: 8%;
    }
  }

  .projects__element--eight {
    display: flex;
    flex-direction: column;

    .projects__info {
      margin-top: 2%;
      margin-left: 8%;
      margin-bottom: 6%;
    }

    .projects__counter {
      flex-basis: auto;
      margin-left: 54%;
      margin-right: auto;
      margin-bottom: 8%;
    }

  }

  .projects__element--nine {
    background-position: center top -30px;

    .projects__info {
      margin-top: 8%;
      margin-left: 58%;
    }

    .projects__counter {
      margin-left: 58%;
      margin-bottom: 8%;
    }
  }

}

@media (max-width: 360px) {

  .projects__element--six {
    background-position: -60px 20px;

    .projects__info {
      margin-left: 26%;
    }

    .projects__counter {
      margin-top: 0;
      margin-left: 58%;
    }
  }
}