.persons__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.persons__item {
  flex-basis: 31%;
  margin-right: 3.4%;

  &:last-child {
    margin-right: 0;
  }
}

.persons__img {
  display: block;
  width: 100%;
}

.persons__name {
  font-size: 13px;
  font-family: "roboto_condensedbold";
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.231;
  margin: 5.5% 0 0 0;

  @media (min-width: 1500px) {
    @include dynamicText(81.25%, 26, 13, 1500px, 3000, 1500);
  }
}

.persons__post {
  font-size: 12px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.333;
  margin: 0 0 5.5% 0;

  @media (min-width: 1500px) {
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

.persons__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.persons__cv {
  font-size: 14px;
  color: $colorPersonIcon;
  text-transform: uppercase;
  line-height: 1.143;
  text-decoration: none;
  padding-right: 25px;
  background: url('../img/arrow-left.svg') right center no-repeat;

  &:hover {

    span {
      border-color: $colorPersonIcon;
    }

  }

  span {
    border-bottom: 1px solid transparent;
    transition: border-color .8s;
  }

  @media (min-width: 1500px) {
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }

}

@media (max-width: 980px) {

  .persons__list {
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  .persons__item {
    flex-basis: 45%;
    margin-right: 0;

    &:nth-child(2) {
      margin-left: 3.4%;
    }

    &:not(:last-child) {
      margin-bottom: 4%;
    }
  }
}

@media (max-width: 650px) {

  .persons__list {
    justify-content: space-between;
  }

  .persons__item {

    &:nth-child(2) {
      margin-left: 0;
    }
  }
}

@media (max-width: 480px) {

  .persons__list {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
  }

  .persons__item {
    flex-basis: auto;
  }

}






